:root {
	--xpos: 500;
	--ypos: 100px;
	--timepos: 100px;

}

h1 {
	position: relative;
	left: 500px;
}

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.wrapper {
  display: flex;
  flex-direction: column;
  margin: 15px;
  justify-content: center;
  text-align: left;

}

.grid {
  width: 350px;
  height: 350px;
  background: transparent;
  /* border: 1px solid #e0e0e0; */
	position: relative;
	margin: 0px;
	display: inline-block;


}

#activegrid {
  border: 3px solid blue;
}

/* .grid:hover {
  background: #fdfdfd;

} */

.row {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;

}

#no {
  display: none;
}

#yes {
  display: block;
	position: relative;
	left: 0px;;
	top: 0p;

}

#popup {
  display: block;
  width: 500px;
  height: 500px;
  background: white;
  border: 1px solid grey;
}

.hoverup {
  display: block;
	position: fixed;
	left:calc(var(--xpos) * 1px);
  top: calc(var(--ypos) * 1px);
  width: 200px;
  height: 75px;
  background: white;
  border: 1px solid grey;
}

.mood-button {
	font-size: 0;
	width: 50px;
	height: 50px;
	border-radius: 50%;
	border: solid 3px transparent;
}

.circle {
  position: absolute;
  width: 25px;
  height: 25px;
  border-radius: 50%;
}

/* .circle:hover {
  transition: 1s;
  width: 50px;
  height: 50px;
} */

textarea {
	width: 100%;
	height: 100px;
	padding: 12px 20px;
	outline: none;
	resize: none;
	box-sizing: border-box;
}

#mood-1 {
background: black;
top: calc((400px / 7)*7);

}

#mood-2 {
	background: grey;
	top: calc((400px / 7)*6);

}

#mood-3 {
	background: green;
	top: calc((400px / 7)*5);

}

#mood-4 {
	background: blue;
	top: calc((400px / 7)*4);

}

#mood-5 {
	background: red;
	top: calc((400px / 7)*3);


}

#mood-6 {
	background: orange;
	top: calc((400px / 7)*2);


}

#mood-7 {
	background: hotpink;
	top: calc((400px / 7)*1);

}

.one {
	position: absolute;
	width: 25px;
	height: 25px;
	border-radius: 50%;
	left: 50px;
}

.two {
	position: absolute;
	width: 25px;
	height: 25px;
	border-radius: 50%;
	left: 150px;
}

.three {
	position: absolute;
	width: 25px;
	height: 25px;
	border-radius: 50%;
	left: 250px;
}

.food {
  position: absolute;
  width: 15px;
  height: 15px;
  border-radius: 50%;
	background: transparent;
	border: 1px solid grey;
	top: calc((400px / 7)*4);
	left: calc(var(--timepos) * 1px);
}

.modal {
display: none;
position: fixed;
z-index: 1;
left: 500px;
top: 150px;
height: 100%;
width: 100%;
overflow: auto;
background-color: rgba(0,0,0,0.5);
}

.modal-content {
	background-color: white;
	/* margin: 20%; auto; */
	position: absolute;
	top: 50%;
	left: 50%;
	width: 500px;
	height: 300px;
	margin-left: -250px;
	margin-top: -150px;
	padding: 30px;
	box-shadow: 0 5px 8px 0 rgba(0,0,0,0.2);
	animation-name: modalopen;
	animation-duration: 1s;

}

.closeBtn {
	color: #ccc;
	float: right;
	font-size: 30px;
}

table {
	border: 1px solid #e0e0e0;
	border-collapse: collapse;
	text-align: left;
	width: 350px;
	height: 350px;
}

td {
	position: relative;
	border-collapse: collapse;
	width: 33%;
	height: 350px;

}

#tablehover {
  background: #fdfdfd;

}

.add {
	background: #F5F3F3;
	color: grey;
	border: none;
	border-radius: 6px;
	width: 100px;
	height: 40px;
	padding: 0px;
	margin: 0px;
	position: absolute;
  top: 140px;
	left: 10px;
}

.foodbutton {
	background: #F5F3F3;
	color: grey;
	border: none;
	width: 60px;
	height: 60px;
	padding: 0px;
	margin: 0px;
	position: absolute;
  top: 260px;
	left: 24px;
	border-radius: 50%;
}

.label {
	color: #C4C4C4;
	position: absolute;
	text-transform: uppercase;
	font-size: 8pt;
	left: 20px;
	top: 40px;
}

.row {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.column-wrapper {
	overflow-x: scroll;
  overflow-y: hidden;
  display: flex;
  white-space: nowrap;
	flex-direction: column;
	flex-wrap: wrap;
	height: 720px;

}

.number {
	color: grey;
	position: absolute;
	top: 5px;
	left: 20px;
	font-size: 16pt;
}
